import styled from '@emotion/styled';

export const Td = styled.td`
  width: calc(100% / 3);
  padding: 10px 10px;
  text-align: center;
  font-size: 15px;
  font-weight: 400;
  background-color: #91b38a;
`;

export const HeadTabl = styled.th`
  text-align: center;
  font-size: 25px;
  font-weight: 400;
  text-transform: uppercase;
  background-color: grey;
  color: white;
  margin: auto;
`;

export const Tabl = styled.table`
  margin-top: 30px;
`;
